<template>
  <div>
    <BasePanel>
      <template #panel-head>
        <router-link to="/">主页</router-link>
        /
        <span>新消息</span>
      </template>
      <template #panel-content>
        <MessView  
        v-for="whoI in messages.hasnot_read_messages" :key="whoI.id" v-bind="whoI"  />
        <!-- 未读消息知识 -->
      </template>
    </BasePanel>
    <BasePanel >
      <template #panel-head>
        <span>过往信息</span>
      </template>
      <template #panel-content>
        <MessView  
        v-for="whoI in messages.has_read_messages" :key="whoI.id" v-bind="whoI"  />
      </template>
    </BasePanel>
  </div>
</template>

<script>
import BasePanel from '../components/layout/BasePanel.vue'
import MessView from '../components/MessView.vue'
import {getMessage,markAllMessage} from '../plugins/api'
export default {
  components: { BasePanel,MessView},
  data(){
    return {
      // has_read_messages: [], // 过往消息
      // hasnot_read_messages:[],// 新消息
      messages: [] // 更改消息状态
    };
  },
  async created() {
    const res = await getMessage(sessionStorage.getItem('accesstoken'))
    console.log(res)
    // 将获取到的 数据 传给 messages
    this.messages = res.data
    // 有未读消息的话 需要标记为已读
    if(res.data.hasnot_read_messages.length){
       markAllMessage(sessionStorage.getItem('accesstoken'))
    }
  }
}
</script>

